$main-color: rgb(255, 255, 255);
$second-color: black;
$transition: all 0.5s;
$spicial: #2d67f7;
$border: 1px solid rgba(184, 184, 184, 0.63);
$shadow: 0 10px 20px rgba(0, 0, 0, 0.2);

.skills {
  min-height: 100vh;
}

.skill-sec {
  padding: 10px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  margin-bottom: 30px;
  .left-sec {
    width: 35%;
    .pic {
      width: 100%;
      height: 100%;
    }
  }
  .right-sec {
    width: 75%;
    min-height: 300px;
    border: 1px solid $second-color;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    padding: 15px;
    .skill {
      width: 100%;
      margin: 8px;
      display: flex;
      justify-content: space-evenly;
      align-items: center;
    }
  }
}

.skill-sec img {
  width: 100%;
  height: 100%;
}

.skill .name {
  width: 25%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  i {
    font-size: 40px;
  }
}

.skills h3 {
  color: $second-color;
  font-size: 18px;
  font-weight: 700;
}

.progress {
  position: relative;
  width: 75%;
  height: 15px;
  border: $border;
  margin: 10px;
  border-radius: 15px;
  span {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    border-radius: 15px;
    transition: width 0.5s 0.3s linear;
    animation-name: spanpro;
    animation-duration: 0.5s;
    animation-timing-function: linear;
    animation-direction: alternate;
    animation-fill-mode: forwards;
    // animation-delay: 0.3s;
  }
  .html2 {
    background-color: #e54d26;
  }
  .css2 {
    background-color: #0b73ba;
  }
  .js2 {
    background-color: #d9b82d;
  }
  .sass {
    background-color: #c66394;
  }
  .gulp {
    background-color: #d34446;
  }
  .react {
    background-color: #5ed3f3;
  }
}

.others {
  padding: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 35px;
}

.card1 {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  border-radius: 10px;
  box-shadow: $shadow;
  border: $border;
  width: 280px;
  height: 170px;
  transition: $transition;
  &:hover {
    background-color: $spicial;
    transform: translateY(-10px);
  }
  i {
    font-size: 45px;
    color: $spicial;
    transition: $transition;
  }
  h4 {
    text-transform: uppercase;
    transition: $transition;
  }
  p {
    width: 90%;
    text-align: center;
    transition: $transition;
  }
}

.card1:hover i,
.card1:hover h4,
.card1:hover p {
  color: $main-color;
}

@keyframes spanpro {
  from{
     
      width: 0;
      }
  to{
     
      width: attr(data-width);
     }
    }